.orderSuccess{
    margin:auto;
    text-align:center;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.orderSuccess > svg{
    font-size: 7vmax;
    color: tomato;
}
.orderSuccess > p{
    font-size: 2vmax;
}
.orderSuccess > a{
    background-color: rgb(51,51,51);
    color: white;
    border: none;
    padding:1vmax 3vmax;
    cursor: pointer;
    font: 400 4vw 'Roboto';
    text-decoration: none;
    margin: 2vmax;
}
.orderSuccess > a:hover{
    background-color: rgb(13, 12, 12); 
}

@media screen and (max-width:60px) {
    .orderSuccess > a{
        padding: 3vw 6vw;
        font: 400 4vw "Roboto";
        margin: 2vmax;
    }
    .orderSuccess > svg{
        font-size: 20vw;

    }
    .orderSuccess > p{
        margin: 2vmax;
        font-size: 5vw;
    }

}